import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import './App.css';

import About from './components/About'
import Header from './components/Header'
import Projects from './components/Projects'
import ProjectWindow from './components/ProjectWindow'

function Footer() {
  return (
    <div className="footer" id="footer">
      <div>
        <h3>Want to get in touch?</h3>
        <p>You can reach me at <span className="bldr">dylanmoylan1@gmail.com</span>.<br /> You can also find me on <span className="bldr"><a className="hover-white" href="https://www.linkedin.com/in/dylan-moylan"><i className="fab fa-linkedin"></i>LinkedIn</a></span> or <span className="bldr"><a className="hover-white" href="https://github.com/dylonion"><i className="fab fa-github-square"></i>Github</a></span>. I'd love to hear from you.</p>
        <a className="mail btn" href="mailto:dylanmoylan1@gmail.com">Send Email</a>
      </div>
    </div>
  )
}
