class App extends React.Component {
  render() {
    return (
      <Router>
        <div className="App">
          <Route path="/" component={Header} />
          <div className="content">
            <Route exact path="/" component={Projects} />
            <Route exact path="/ofHome" render={(props) => {
              return (
                  <ProjectWindow project="ofHome" />
                )
              }}
            />
            <Route exact path="/selectim" render={(props) => {
              return (
                  <ProjectWindow project="selectim" />
                )
              }}
            />
            <Route exact path="/this-site" render={(props) => {
              return (
                  <ProjectWindow project="thisSite" />
                )
              }}
            />
            <Route exact path="/valerie" render={(props) => {
              return (
                  <ProjectWindow project="valerie" />
                )
              }}
            />
            <Route exact path="/about" component={About} />
          </div>
          <Route path="/" component={Footer} />
        </div>
      </Router>
    )
  }
}

export default App;
