import React from 'react'

function RelatedLinks(props) {
  return (
    <div className="links">
      <a className={props.site ? 'btn' : 'btn nodisplay'} href={props.site ? props.site : ''}>Visit the Site</a>
      <a className={props.github ? 'btn' : 'btn nodisplay'} href={props.github ? props.github : ''}>View the Github repo</a>
    </div>
  )
}

export default RelatedLinks
